import Vue from 'vue'

const baseUrl = process.env.VUE_APP_SERVER_URL
const headers = {
    // Authorization: 'Bearer ...' // token
}

export default {
    memberBoards: {
        // 
        create(data) {
            return Vue.axios({
                headers,
                url: `${baseUrl}/memberBoards`,
                method: 'POST',
                data: data.data,
            })
        },
        //  
        read(data) {
            return Vue.axios({
                headers,
                url: `${baseUrl}/memberBoards/${data.id}`,
                method: 'GET',
            })
        },
        // 
        update(data) {
            return Vue.axios({
                headers,
                url: `${baseUrl}/memberBoards/${data.id}`,
                method: 'PUT',
                data: data.data,
            })
        },
        // 
        delete(data) {
            return Vue.axios({
                headers,
                url: `${baseUrl}/memberBoards/${data.id}`,
                method: 'DELETE',
            })
        },
        //  
        search(data) {
            return Vue.axios({
                headers,
                url: `${baseUrl}/memberBoards`,
                method: 'GET',
                params: data.params
            })
        },
    },
}
