<template>
  <div>
    <v-data-table
        :headers='headers'
        :items='items'
        :serverItemsLength='totalItems'
        :options.sync='options'
        :loading='isLoading'
        loadingText='  ...'
        noDataText=' .'

        :footerProps='{
                showFirstLastPage: true,
                pageText: `${options.page}/${totalPages} `,
                itemsPerPageText: `  `,
                itemsPerPageOptions: [10, 20, 50],
            }'
    >
      <template v-slot:top>
        <div class='text-h6 font-weight-bold pl-8 pt-6'>
           ({{ totalItems }})
          <v-btn icon @click='toggleIsOpen'>
            <v-icon>
              mdi-plus-circle
            </v-icon>
          </v-btn>
        </div>
      </template>
      <template v-slot:item.actions='{ item }'>
        <v-btn @click='handleEditClick(item)' icon class='mr-1'>
          <v-icon color='blue'>
            mdi-pencil
          </v-icon>
        </v-btn>
        <v-btn @click='handleRemoveClick(item)' icon>
          <v-icon color='red'>
            mdi-delete-sweep
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import Resource from '@/api/server.js'
export default {
  data: () => ({
    headers: [
      { text: '', value: 'id', align: 'center', width: '10%' },
      { text: '', value: 'name', align: 'center' },
      { text: '', value: 'title', align: 'center' },
      { text: ' / ', value: 'actions', align: 'center', sortable: false },
    ],
    items: [],
    totalItems: 0,
    totalPages: 0,
    isLoading: true,

    isOpen: false,
    activeId: null,
    inputValues: { title: '', name: '' },
    requiredRules: [
      v => !!v || ' .', ],
    options: {},
}),
  methods: {
    toggleIsOpen() {
      this.isOpen = !this.isOpen
    },

    // Event Handlers
    handleEditClick(item) {
      this.readApi(item.id)
    },
    handleSaveClick() {
      const isValidate = this.$refs.form.validate()
      if (!isValidate) return

      if (!this.activeId) {
        // 
        const isConfirm = confirm(' ?')
        if (!isConfirm) return

        this.createApi()
      } else {
        // 
        const isConfirm = confirm(' ?')
        if (!isConfirm) return

        this.updateApi(this.activeId)
      } },
    handleRemoveClick(item) {
      const isConfirm = confirm('[ ' + item.title + ' ] ()  ?')
      if (!isConfirm) return

      this.deleteApi(item.id)
    },

    // APIs
    searchApi(options) {
      this.isLoading = true
      if (options) this.options = options
      const { page, itemsPerPage, sortBy, sortDesc } = this.options
      Resource.memberBoards
          .search({
            params: {
              size: itemsPerPage,
              page: page - 1 || undefined,
              sort: sortBy?.length ? `${sortBy[0]},${sortDesc[0] ? 'DESC' : 'ASC'}` : undefined,
            },
          })
          .then((res) => {
            this.items = res.data._embedded.persons
            this.totalItems = res.data.page.totalElements
            this.totalPages = res.data.page.totalPages
          })
          .catch((err) => {
            console.error(err)
          })
      this.isLoading = false
    },
    createApi() {
      Resource.memberBoards
          .create({ data: this.inputValues })
          .then(() => {
            this.searchApi()
            alert(' !')
          })
          .catch((err) => {
            console.error(err)
            alert(' !')
          })

      this.toggleIsOpen()
    },
    readApi(id) {
      Resource.memberBoards
          .read({ id })
          .then((res) => {
            const item = res.data
            this.activeId = item.id
            this.inputValues.title = item.title
            this.inputValues.age = item.age
            this.toggleIsOpen()
          })
          .catch((err) => {
            console.error(err)
            alert(' !')
          })
    },
    updateApi(id) {
      Resource.memberBoards
          .update({ id, data: this.inputValues })
          .then(() => {
            alert(' !')
          })
          .catch((err) => {
            console.error(err)
            alert(' !')
          })

      this.toggleIsOpen()
    },
    deleteApi(id) {
      Resource.memberBoards
          .delete({ id })
          .then(() => {
            alert(' !')
          })
          .catch((err) => {
            console.error(err)
            alert(' !')
          })

      this.toggleIsOpen()
    },
  },
}
</script>